import React from 'react'

import DrumPad from '../DrumPad/DrumPad'

import styles from './DrumPadSection.module.css';


export default function DrumPadSection(props) {
    const drumpadsArray = props.drumpads.map(drumpad => 
        {
            return (
            <DrumPad
            key={drumpad.DrumPadID}
            DrumPadID={drumpad.DrumPadID}
            DrumPadSRC={drumpad.DrumPadSRC}
            DrumPadName={drumpad.DrumPadName}
            DrumPadonClickHandler={props.DrumPadonClickHandler}/>
            )
        });


    return (
        <div className={styles.DrumPadSection}>
            {drumpadsArray}
        </div>
    )
}


