import React from 'react'

import styles from './DrumPad.module.css';

export default function DrumPad(props) {
    return (
        <div className='drum-pad' 
            id={props.DrumPadName} 
            onClick={() => props.DrumPadonClickHandler(props.DrumPadID)}>
            <div className={styles.DrumPad}>     
                <audio 
                    className='clip'
                    id={props.DrumPadID}
                    src={props.DrumPadSRC} autoPlay={false} ></audio>
                <button> 
                    {props.DrumPadID}
                </button>
            </div>
        </div>
    )
}
