import React, { Component } from 'react'

import DrumPad from '../DrumPad/DrumPad'
import Display from '../Display/Display'
import DrumPadSection from '../DrumPadSection/DrumPadSection'

import styles from './DrumMachine.module.css';

// const axios = require('axios');

export default class DrumMachine extends Component {
    constructor(props) {
        super(props);
        this.state = {
            drumpads: [
                {DrumPadID: 'Q', 
                DrumPadSRC: "https://s3.amazonaws.com/freecodecamp/drums/Heater-1.mp3",
                DrumPadName: 'Heater-1'},
                {DrumPadID: 'W', 
                DrumPadSRC: "https://s3.amazonaws.com/freecodecamp/drums/Heater-2.mp3",
                DrumPadName: 'Heater-2'},
                {DrumPadID: 'E', 
                DrumPadSRC: "https://s3.amazonaws.com/freecodecamp/drums/Heater-3.mp3",
                DrumPadName: 'Heater-3'},
                {DrumPadID: 'A', 
                DrumPadSRC: "https://s3.amazonaws.com/freecodecamp/drums/Heater-4_1.mp3",
                DrumPadName: 'Heater-4'},
                {DrumPadID: 'S', 
                DrumPadSRC: "https://s3.amazonaws.com/freecodecamp/drums/Heater-6.mp3",
                DrumPadName: 'Clap'},
                {DrumPadID: 'D', 
                DrumPadSRC: "https://s3.amazonaws.com/freecodecamp/drums/Dsc_Oh.mp3",
                DrumPadName: 'Open-HH'},
                {DrumPadID: 'Z', 
                DrumPadSRC: "https://s3.amazonaws.com/freecodecamp/drums/Kick_n_Hat.mp3",
                DrumPadName: "Kick-n'-Hat"},
                {DrumPadID: 'X', 
                DrumPadSRC: "https://s3.amazonaws.com/freecodecamp/drums/RP4_KICK_1.mp3",
                DrumPadName: 'Heater-8'},
                {DrumPadID: 'C', 
                DrumPadSRC: "https://s3.amazonaws.com/freecodecamp/drums/Cev_H2.mp3",
                DrumPadName: 'Closed-HH'},
            ]   
             ,
            activeDrumPadID: ''
        }
        
        this.DrumPadonClickHandler = this.DrumPadonClickHandler.bind(this)
        this.onKeyDownHandler = this.onKeyDownHandler.bind(this)
    }



    componentDidMount () {
        
        document.addEventListener("keydown",this.onKeyDownHandler)

    }


    DrumPadonClickHandler (DrumPadID) {  

        document.getElementById(DrumPadID).play();

        this.setState({
            activeDrumPadID: DrumPadID
        })

    }



    onKeyDownHandler (event) {  

        const keyArray = ['Q', 'W', 'E', 'A', 'S', 'D', 'Z', 'X', 'C']
        const keyDown = event.key.toUpperCase()

        if (keyArray.indexOf(keyDown) !== -1) {

            document.getElementById(keyDown).play();

            this.setState({
                activeDrumPadID: keyDown
            })
        }

    }



    render() {

        // const drumpadsArray = this.state.drumpads.map(drumpad => 
        //     {
        //         return (
        //         <DrumPad
        //         key={drumpad.DrumPadID}
        //         DrumPadID={drumpad.DrumPadID}
        //         DrumPadSRC={drumpad.DrumPadSRC}
        //         DrumPadName={drumpad.DrumPadName}
        //         DrumPadonClickHandler={this.DrumPadonClickHandler}/>
        //         )
        //     });

        let activeDrumPadName = '';
        for (let drumpad of this.state.drumpads) {
            if (this.state.activeDrumPadID === drumpad.DrumPadID) {
                activeDrumPadName = drumpad.DrumPadName
            }
        }

        return (
            <div id="drum-machine" 
                className={styles.DrumMachine}
                onKeyDown={this.onKeyDownHandler}>
                {/* {drumpadsArray} */}
                <DrumPadSection drumpads={this.state.drumpads} DrumPadonClickHandler={this.DrumPadonClickHandler}/>
                <Display DrumPadName={activeDrumPadName}/>
            </div>
        )
    }

}

